
<template>
  <div class="bidding">
    <Modal
      v-model="isPop"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled">请添加粮食直补信息</span>
        <span v-else>粮食直补信息</span>
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :rules="ruleValidateType"
            :label-width="85"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="资金类别"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写资金类别"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="主管单位"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写主管单位"
                ></Input>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="核查批次"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写核查批次"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="核查对象"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写核查对象"
                ></Input>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="镇"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写镇"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="村"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写村"
                ></Input>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="证件类型"
                prop="code"
              >
                <CheckboxGroup v-model="form.bidding_unit">
                  <Checkbox label="身份证"></Checkbox>
                  <Checkbox label="营业执照"></Checkbox>
                  <Checkbox label="其他"></Checkbox>
                </CheckboxGroup>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="证件号码"
                prop="idNum"
              >
                <Input
                  v-model.trim="form.idNum"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写证件号码"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="联系方式"
                prop="phone"
              >
                <Input
                  v-model.trim="form.phone"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写联系方式"
                ></Input>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="申报面积"
                prop="applayArea"
              >
                <Input
                  v-model.trim="form.applayArea"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写申报面积"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="核定面积"
                prop="applyArea"
              >
                <Input
                  v-model.trim="form.applyArea"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写核定面积"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="耕种条件"
                prop="code"
              >
                <CheckboxGroup v-model="form.bidding_unit">
                  <Checkbox label="符合要求"></Checkbox>
                  <Checkbox label="不符合要求"></Checkbox>
                </CheckboxGroup>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="种植作物"
                prop="code"
              >
                <CheckboxGroup v-model="form.bidding_unit">
                  <Checkbox label="小麦"></Checkbox>
                  <Checkbox label="玉米"></Checkbox>
                  <Checkbox label="水稻"></Checkbox>
                  <Checkbox label="甘薯"></Checkbox>
                  <Checkbox label="马铃薯"></Checkbox>
                  <Checkbox label="其他"></Checkbox>
                </CheckboxGroup>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="备注"
                prop="bidding_unit"
              >
                <Input v-model="form.bidding_unit" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请填写备注"></Input>
              </FormItem>
              </Col>
            </Row>
          </Form>
        </TabPane>
        <TabPane
          label="附件"
          name="name7"
        >
          <mulUploadFile ref="uploadFile" :is-disabled="isDisabled" :upload-list="uploadList" />
        </TabPane>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="handleCancel('form')"
        >
          取消
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
      </p>
    </Modal>
  </div>
</template>
/* eslint-disable */
<script>
/* eslint-disable */
  import mulUploadFile from '@/components/mulUploadFile.vue'
  export default {
    name: 'Bidding',
    components: { mulUploadFile },
    props: {
      isDisabled: {
        type: Boolean,
        default: false
      },
      isCreate: {
        type: Boolean,
        default: false
      },
      organizationalForm: {
        type: Object,
        default: () => {}
      },
      billType: {
        type: Object,
        default: () => {}
      }
    },
    data() {
      return {
        uploadList: {
          1: '证件',
          2: '一卡通',
          3: '核查记录表',
          4: '坐标表',
          5: '核定图',
          6: '图像记录'
        },
        // 弹出框内容显示隐藏   默认隐藏
        isPop: false,
        // 添加表单的内容字段  ->  传递数据的字段
        form: {
          id: null,
          bidding_unit: '',
          person_charge: '',
          code: '',
          phone: '',
          bidding_name: '',
          organizational_form: '',
          type: '',
          start_bidding_time: '',
          proxy_org: '',
          winning_bid_org: '',
          winning_bid_money: null,
          remark: ''
        },
        // 表单内容的校验规则
        ruleValidateType: {
          bidding_unit: [
            { required: true, message: '请填写招投标单位', trigger: 'blur' }
          ],
          person_charge: [
            { required: true, message: '请填写负责人', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '请填写联系电话', trigger: 'blur' }
          ],
          bidding_name: [
            { required: true, message: '请填写标段名称', trigger: 'blur' }
          ],
          code: [
            { required: true, message: '请填写编号', trigger: 'blur' }
          ],
          organizational_form: [
            { type: 'number', required: true, message: '请选择组织形式', trigger: 'change' }
          ],
          bidding_type: [
            { type: 'number', required: true, message: '请选择招标类型', trigger: 'change' }
          ],
          start_bidding_time: [
            { type: 'date', required: true, message: '请填写开标时间', trigger: 'blur' }
          ],
          proxy_org: [
            { required: true, message: '请填写代理机构', trigger: 'blur' }
          ],
          winning_bid_org: [
            { required: true, message: '请填写中标单位', trigger: 'blur' }
          ],
          winning_bid_money: [
            { type: 'number', required: true, message: '请填写中标金额', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      // 取消操作
      handleCancel(name) {
        this.$refs[name].resetFields();
        this.isPop = false;
      },
      // 确认操作   关系后台数据
      handleConfirm(name) {
        const that = this;
        that.$refs[name].validate((valid) => {
          if (valid) {
            const isCreate = that.isCreate
            const method = isCreate ? '/bidding/addBidding' : '/bidding/updateBidding'
            const form = Object.assign({}, that.form)
            form.start_bidding_time = new Date(form.start_bidding_time).getTime()
            form.relationFileIds = this.$refs.uploadFile.getFileIds()
            that.$Spin.show();
            that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isPop = false
                that.$Message.success(res.msg);
                that.$emit('refreshList')
              } else {
                that.$Message.error(res.msg);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.$Message.error('请填写好信息');
          }
        })
      },
      showModal(id) {
        const that = this;
        that.clearForm();
        if (id) {
          // this.$nextTick(() => {
          //   this.$refs.uploadFile.getFileItems()
          // })
          that.$Spin.show();
          that.commonUtil.ajaxGetUtil('/bidding/searchBiddingDetails/' + id, {}, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              const form = res.data
              form.start_bidding_time = new Date(form.start_bidding_time)
              that.form = form
              that.$nextTick(() => {
                that.$refs.uploadFile.getFileItems(form.relationFileids)
                that.isPop = true
              })
            } else {
              that.$Message.error(res.msg);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.isPop = true
        }
      },
      clearForm() {
        this.form = {
          id: null,
          bidding_unit: '',
          person_charge: '',
          phone: '',
          code: '',
          bidding_name: '',
          organizational_form: '',
          type: '',
          start_bidding_time: '',
          proxy_org: '',
          winning_bid_org: '',
          winning_bid_money: null,
          remark: ''
        }
        this.$refs.form.resetFields();
        const uploadFile = this.$refs.uploadFile
        if (uploadFile) {
          uploadFile.clearFileItems()
        }
      }
    }
  }
</script>



